.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


/*---- Background-Color -----*/
.bg-darkblue { background: rgb(0,0,0);
  background: linear-gradient(0deg, rgba(0,0,0,1) 0%, rgba(27,38,99,1) 100%); }

.bg_video {
  background: rgb(11,15,41);
  background: linear-gradient(180deg, rgba(11,15,41,1) 0%, rgba(0,255,153,1) 100%);
}

.bg_folder {
  background: rgb(0,0,0);
background: linear-gradient(180deg, rgba(0,0,0,1) 0%, rgba(27,38,99,1) 100%);
}

.bg_rojo {
  background: rgb(255,42,48);
background: linear-gradient(0deg, rgba(255,42,48,1) 0%, rgba(15, 15, 15, 0) 56%);
}
