/*==========RESPONSIVE===========*/
/*Mobil*/
@media screen and (max-width:512px){
.bg_rojo h3 {
    font-size: 26px;
}

.bg_rojo a {
    font-size: 20px;
}


}
/*Tablet*/
@media (min-width: 513px) and (max-width:821px){
    .bg_rojo a {
        font-size: 30px;
    }
}
/*Desktop*/
@media screen and (min-width:821px){
    .bg_rojo figure {
        text-align: end;
    }

    .bg_rojo {
        min-height: 100vh!important;
    }
}