    content: "";
    width: 100%;
    height: 100%;
    border: 0.2px solid #fff;
    border-radius: 50%;
    opacity: 0.10;
    position: absolute;
    left: 0;
  }
  
  .video_icon:before {
    transform: scale(2.5);
  }
  
  .video_icon:after {
    transform: scale(3.8);
  }
  
  .video_icon i {
    color: #17c7bd;
    text-shadow: 0px 6px 25px rgba(16, 147, 140, 0.9);
    position: relative;
  }
  
  .video_icon:hover {
