.profile--slider .swiper-pagination-bullet {
    display: inline-block;
    width: 40px;
    height: 10px;
    margin: 0 9px;
    background: #ffffff;
    border-radius: 4px;
    cursor: pointer;
    
}

.profile--slider .swiper-slide {
    padding-bottom: 40px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.folder-child {
    background: url('./../../../img/landing-new/folder-interna.webp') no-repeat center center;
    background-size: 100% 100%;
    padding: 0 30px;
}

.folder-child p {
    display: inline-block;
    padding-bottom: 3px;
    position: relative;
    transition: all .1s linear;
    text-transform: uppercase;
}

  .folder-child p:before {
    bottom: 0;
    content: "";
    height: 1.5px;
    position: absolute;
    transition: width .2s linear;
    background: #ffffff;
    left: 0;
    right: auto;
    width: 100%;
}

.profile--slider {
    background: url('./../../../img/landing-new/folder-father.webp') no-repeat center center;
    background-size: 100% 100%;
    padding: 0 50px 40px;
    width: 100%;
    overflow: hidden;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    align-content: stretch;
}


/*Mobil*/
@media screen and (max-width:512px){
.profile--slider h2 {
    font-size: 30px;
    width: 75% !important;
}

.profile--slider {
    padding: 50px 20px 20px;
   
}
.folder-child {
    background-size: 90% 100%;
}



}
/*Tablet*/
@media (min-width: 513px) and (max-width:820px){
    .profile--slider h2 {
        font-size: 30px;
        width: 75% !important;
    }
    .profile--slider {
        padding: 50px 20px 20px;
       
    }
    .folder-child {
        background-size: 90% 100%;
    }
}
/*Desktop*/
@media screen and (min-width:821px){
    .profile--slider {
        height: 650px;
        width: 100%;
    }

    .profile--slider h2 {
        font-size: 60px;
    }
}