.bg-wrap-border {
    border-bottom-right-radius: 2.5rem;
    border-bottom-left-radius: 2.5rem;
}


.bg_rojo_fondo {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    background: -webkit-linear-gradient(0deg, rgba(255,42,48,1) 0%, rgba(0, 0, 0, 0.4) 56%), url("../../img/landing-new/gptw-landing.jpg") no-repeat center;
    background: -o-linear-gradient(0deg, rgba(255,42,48,1) 0%, rgba(0,0,0,0.4) 56%), url("../../img/landing-new/gptw-landing.jpg") no-repeat center;
    background: linear-gradient(0deg, rgba(255,42,48,1) 0%, rgba(0,0,0,0.4) 56%), url("../../img/landing-new/gptw-landing.jpg") no-repeat center;
    background-size: cover;
    -webkit-box-shadow: 0px 50px 70px 0px rgba(0, 11, 40, 0.24);
    box-shadow: 0px 50px 70px 0px rgba(0, 11, 40, 0.24);
    height: auto;
    max-width: 100%;
    margin: 0 auto;
    position: relative;
    z-index: 1;
  }


  .btn-folder  {
    background: rgb(69,98,255);
    background: linear-gradient(90deg, rgba(69,98,255,1) 0%, rgba(21,219,255,1) 100%);
    color: white; /* Cambia el color del texto para asegurar la legibilidad */
    padding: 15px 100px; /* Ajusta el relleno (padding) según tus necesidades */
    border: none; /* Elimina el borde si lo prefieres */
    cursor: pointer;
  }
  
/*==========RESPONSIVE===========*/
/*Mobil*/
@media screen and (max-width:512px){
  .btn-folder {
    padding: 12px 0; /* Ajusta el relleno horizontal para adaptarse al ancho completo */
    text-align: center; /* Centra el texto si es necesario */
    width: 100%;
    display: block;
}
}
