.flotante {
    position: fixed;
    bottom: 5%;
    right: 0;
    width: 5rem;
    height: auto;
    border-radius: 0 15px 15px 0;
    border-left: 0;
    z-index: 999999;
    transition: all .2s linear
  }

  .flotante a {
    width: 46px;
    height: 46px;
    border-radius: 50%;
    font-size: 18px;
    line-height: 48px;
    text-align: center;
    transition: all 0.2s linear;
    display: inline-block;
    color: #fff;
    background: #128C7E;
    border-color: #128C7E;
  }

  .flotante a i {
    color: #fff;
    font-size: 1.5rem;
  }

  .flotante a:hover {
    font-size: 20px;
    width: 49px;
    height: 49px;

    background: #25D366;
    border-color: #25D366;
  }